import React, { Component } from 'react';
import { RobotService } from '../services/RobotService';

export default class RobotDetails extends Component {
  state = {
    robot: null,
  };

  async componentDidMount() {
    const id = this.props.match.params.id;
    const robot = await RobotService.getRobotById(id);
    this.setState({ robot });
  }

  onSendGiftClickHandler = async (ev) => {
    // update robot
    const robot = { ...this.state.robot };
    robot.giftsCount++;
    const updatedRobot = await RobotService.saveRobot(robot);
    this.setState({ robot: updatedRobot });
  };

  onGoBackClickHandler = (ev) => {
    this.props.history.go(-1);
  };

  componentDidUpdate(prevProps, prevState) {
    // for handling changes without componentDidMount
  }

  render() {
    const { robot } = this.state;

    if (!robot) {
      return <div>Loading! :) </div>;
    }

    // FOR EXAMPLE
    const imgStyle = {
      height: '100px',
    };

    return (
      <div>
        <button onClick={this.onGoBackClickHandler}>Go Back</button>
        <h1>{robot.name}</h1>
        <div>{'üéÅ'.repeat(robot.giftsCount)}</div>
        <button onClick={this.onSendGiftClickHandler}>Send üéÅ</button>
        <img
          style={imgStyle}
          src={`https://robohash.org/${robot.name}.png`}
          alt=""
        />
      </div>
    );
  }
}
