import React, { Component } from 'react';

import { RobotService } from '../services/RobotService';

import RobotList from '../cmps/RobotList';
import RobotFilter from '../cmps/RobotFilter';

export default class RobotPage extends Component {
  state = {
    robots: [],
    filterBy: {
      term: '',
      color: '',
      isActive: false,
    },
  };

  async componentDidMount() {
    this.loadRobots();
  }

  loadRobots = async () => {
    const robots = await RobotService.getRobots(this.state.filterBy);
    this.setState({ robots: robots });
  };

  onFilterHandler = (filterBy) => {
    console.log('hi', filterBy);
    this.setState((prevState) => {
      return {
        filterBy: {
          ...prevState.filterBy,
          ...filterBy,
        },
      };
    }, this.loadRobots);
  };

  render() {
    return (
      <div>
        <RobotFilter
          filterBy={this.state.filterBy}
          onFilter={this.onFilterHandler}
        />
        ROBOTS SHOULD BE HERE
        <RobotList robots={this.state.robots} />
      </div>
    );
  }
}
